
// Import bootstrap
@import "~nr1-bootstrap-theme/dist/main.css";

.jumbotron {
    padding-top: 3rem;
    padding-bottom: 3rem;
    margin-bottom: 0;
    background-color: #fff;
}

@media (min-width: 768px) {
    .jumbotron {
        padding-top: 6rem;
        padding-bottom: 6rem;
    }
}

.jumbotron p:last-child {
    margin-bottom: 0;
}

.jumbotron h1 {
    font-weight: 300;
}

.jumbotron .container {
    max-width: 40rem;
}


.header {
    border-bottom: 1px solid #eee;
    padding: 10px 0 15px 0;
}

.header a:hover {
    text-decoration: none;
}

h1 {
    font-size: 2em;
}

h4 {
    padding: 10px 0 5px 0;
}

h5 {
    padding: 15px 0 5px 0;
}

.text-small {
    font-size: 0.8em;
}

.description {
    padding: 10px;
}

.loading {
    padding-top: 50px;
}

.card-title {
    padding: 3px 0 5px 0;
}
